<template>
  <div>
    <jdy-alert title="" @closeAlert="closeAlert" v-if="alertPriceDetailFlag" class="dialog-lv dialog-price-detail">
      <el-row class="scroll-cont table-wrap p10">
        <el-table :data="priceDetail"  border height="206" :summary-method="getSummaries" show-summary sum-text="">
          <el-table-column label="" prop="opPriceName"  width="140"></el-table-column>
          <el-table-column label="" prop="opNum" width="70"></el-table-column>
          <el-table-column label="" prop="opPrice" width="160" class-name="red-font">
            <template scope="scope">
              {{ scope.row.opPrice | moneyTwoPoints }}
            </template>
          </el-table-column>
          <el-table-column label="" prop="opTotalPrice" width="160" class-name="red-font">
            <template scope="scope">
              {{ scope.row.opTotalPrice | moneyTwoPoints }}
            </template>
          </el-table-column>
          <el-table-column label="" prop="opRemark"></el-table-column>
        </el-table>
      </el-row>
    </jdy-alert>
  </div>
</template>

<script>
  import jdyAlert from '@/components/Alert';
  import orderApi from './../api/index';
  export default{
    components:{
      jdyAlert
    },
    props:['priceDetail'],
    data(){
      return{
        alertPriceDetailFlag:false,
      }
    },
    created(){
      const vm = this;
    },
    methods:{
      closeAlert(){ //
        this.alertPriceDetailFlag = false;
        $('.alertbgg').remove();
      },
      priceDetailDialogShow(){
        this.alertPriceDetailFlag = true;
        this.$nextTick(function () {
          jdyFn.setAlertStyle('dialog-price-detail');
        })
      },
      // 
      getSummaries(param){
        const vm = this;
        const { columns, data } = param;
        const sums = [];
        columns.forEach((column, index) => {
          if (index === 0) {
            sums[index] = '';
            return;
          }
          const values = data.map(item => Number(item[column.property]));
          if (!values.every(value => isNaN(value))) {
            sums[index] = values.reduce((prev, curr) => {
              const value = Number(curr);
              const prev1= Number(prev);
              if (!isNaN(value)) {
                return jdyFn.moneyTwoPoints(prev1 + curr);
              } else {
                return jdyFn.moneyTwoPoints(prev1);
              }
            }, 0);
            sums[index] = '' + sums[index];
          } else {
            sums[index] = '';
          }
        });
        return sums;
      },
    }

  }

</script>
<style scoped>
  .dialog-price-detail{
    width: 900px;
  }

</style>




