  height: 175px;
}
.alertHead {
  width: 100%;
  height: 50px;
  background: #f9fafc;
  border-bottom: 1px solid #d7dfe3;
}
.touristHead {
  width: 100%;
  height: 50px;
  background: #f9fafc;
  border-bottom: 1px solid #d7dfe3;
