    getVouchersData(id) {
      this.$http
        .post(api_prefix + "/Order/getVouchers", {
          orderId: id,
          fStatus: 0
        })
        .then(
          response => {
            if (response.data.code == 0) {
              this.getVouchers = response.data.body;
              if(this.getVouchers.length == 1){
                  if(this.getVouchers[0].fStatus == 1){
                      this.showSave == false;
                  }else{
                      this.showSave == true;
                  }
              }
            } else {
              this.$alert(response.data.message);
            }
          },
          response => {
            console.log("");
