    closeAlert() {
      this.btnFlag = false;
      this.orderChangeType = [];
      this.priceChangeObj.flag = false;
      this.offlinePayObj.flag = false;
      this.cancelOrderObj.flag = false;
      this.checkContractObj.flag = false;
