    // 
    refresh() {
      (this.ruleform = {
        currPage: 1,
        pageSize: 10,
        statuses: [], //
        oStatus:'',
        dateType: 2, //
        oPayMethod: null,//
        oType: null,//
        dateStart: null, //
        dateEnd: null, //
        oBuyerCompanyId: null, //id
        oBuyerId: null, //id
        orderType: 0, //:
        pBrand: null, //id
        oProductId: null, //id
        // pTypes:[],//
        pTypes: [],
        oSalerCompanyId: '', //id
        oSalerId: null, //id
        oPayMethod: null, //
        searchKey: "", //
        export: false, //
        alterTicketStatus:null
      }),
        (this.datePicker = null); //
      this.fullscreenLoading = true;
      setTimeout(() => {
        this.fullscreenLoading = false;
        this.$message({
          showClose: true,
          message: "",
          type: "success"
        });
        this.currentPage = 1;
        this.getTableData();
      }, 1000);
    },
    // 
    orderOut() {
      if(this.ruleform.dateStart==null){
        return this.$message.info("");
      }
      let params = JSON.parse(JSON.stringify(this.ruleform));
      let days = window.DateDiff(params.dateEnd, params.dateStart);
      if (days > 31) {
        return this.$message.info("31");
      }
      for (let i = 0; i < params.pTypes.length; i++) {
        switch (params.pTypes[i]) {
          case "":
            params.pTypes[i] = 10;
            break;
          case "":
            params.pTypes[i] = 11;
            break;
          case "":
            params.pTypes[i] = 20;
            break;
          case "":
            params.pTypes[i] = 30;
            break;
          case "":
            params.pTypes[i] = 40;
            break;
          case "":
            params.pTypes[i] = 50;
            break;
          case "":
            params.pTypes[i] = 68;
            break;              
        }
      }
      params.export = true;
      this.downloadUrl = `${api_prefix}Order/export?`;
      if (params.oStatus != '') {
        let tempstaut = "oStatus=";
        tempstaut += params.oStatus;
        this.downloadUrl += `${tempstaut}&`;
      }
    //   if (params.pTypes.length != 0) {
    //     let temppTypes = "statuses=";
    //     temppTypes += params.temppTypes.join(",");
    //     this.downloadUrl += `${temppTypes}&`;
    //   }
      delete params.pTypes;
      delete params.statuses;
      for (let key in params) {
        if (params[key] !== null && params[key] !== "") {
          this.downloadUrl += `${key}=${params[key]}&`;
        }
      }
      location.href = this.downloadUrl;
    },
    // 
    remote_pBrand(key) {
      let param = {
        pageSize: 20,
        currPage: 1,
        bDel: 0,
        bCompanyId: this.logindata.uCompanyId,
