export const validateDate = (rule, value, callback) => {
  var strRegex = /^(\d{4})-(\d{2})-(\d{2})$/;
  if(value.trim() == '') {
    callback();
  }
  else if(!strRegex.test(value)){
    callback(new Error(''));
  }else{
    callback();
  }
}
