export const validateTime = (rule, value, callback) => {

  var strRegex = /^(\d+)-(\d{2})-(\d{2}) (\d{2}):(\d{2}):(\d{2})$/;
 if(value.trim() == '') {
    callback();
  }
  else if(!strRegex.test(value)){
    callback(new Error(''));
  }else{
    callback();
  }
}

