export const validateMoney = (rule, value, callback) => {
  var reg = /(^[1-9]([0-9]+)?(\.[0-9]{1,2})?$)|(^(0){1}$)|(^[0-9]\.[0-9]([0-9])?$)/;
  // var reg = /(^[1-9](\d+)?(\.\d{1,2})?$)|(^(0){1}$)|(^\d\.\d{1,2}?$)/;
  // var reg = /^((\d+)|0)(\.\d{1,2})?$/;
  if (!value) {
    callback();
  }
  else if (value.toString().trim() == '') {
    callback();
  }
  else if(!reg.test(value)){ 
      callback(new Error('99.88'));
    }else{
    callback();
  }
}

