export const integerMoney = (rule, value, callback) => {
  var reg = /^[1-9]\d*$/;
  if (!value) {
    callback();
  }
  else if (value.toString().trim() == '') {
    callback();
  }
  else if(!reg.test(value)){ 
      callback(new Error(''));
    }else{
    callback();
  }
}




