import Vue from 'vue'


//
Vue.filter('orderStatus',function (val) {
  let returnVal = '';
  const num = Number(val);
  if(num == 0){
    returnVal =''
  }
  else if(num == 1){
    returnVal =''
  }
  else if(num == 2){
    returnVal =''
  }
  else if(num == 3){
    returnVal =''
  }
  else if(num == 4){
    returnVal =''
  }
  else if(num == 6){
    returnVal =''
  } 
  else if(num == 10){
    returnVal =''
  }  
  else if(num == 11){
    returnVal =''
  }    
  return returnVal;
});

