// ( 1, 0)
Vue.filter('billStatus',function (val,billType) {
  let returnVal = '';
  const billTypeNum = Number(billType);
  const num = Number(val);
  if(billTypeNum == 0){ //0
    if(num == 0){
      returnVal =''
    }
    else if(num == 1){
      returnVal =''
    }
    else if(num == 2){
      returnVal =''
    }
    else if(num == 3){
      returnVal =''
    }
    else if(num == 4){
      returnVal =''
    }
  }
  else if(billTypeNum == 1){ //1
    if(num == 0){
      returnVal =''
    }
    else if(num == 1){
      returnVal =''
    }
    else if(num == 2){
      returnVal =''
    }
    else if(num == 3){
      returnVal =''
    }
  }
  else {
    returnVal ='-'
  }
  return returnVal;
});


